.main-game {
    flex: 3;
    padding: 20px;
    text-align: center;
  }
  
  .character-display {
    margin-bottom: 20px;
  }
  
  .character-display img {
    width: 150px; /* Adjust size accordingly */
    margin-bottom: 10px;
  }
  
  .character-display input {
    width: 50%;
    padding: 8px;
    font-size: 16px;
  }
  
  .guess-table {
    width: 100%;
    margin-top: auto; /* This pushes the table to the bottom if there's space */
}

table {
    width: 100%;
    border-collapse: collapse;
}

th, td {
    border: 1px solid #ddd;
    text-align: left;
    padding: 8px;
}

thead {
    background-color: #f4f4f4;
}

.correct {
  color: green;
}

.incorrect {
  color: red;
}
